<template>
  <div class="wraper">
    <swiper :options="swiperOption" v-if="showSwiper">
      <!-- slides -->
      <swiper-slide v-for="item in list" :key="item.id">
        <img class="swiper_img" :src="item.imgUrl" alt="error!">
      </swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeSwiper',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        loop: true,
        autoplay: 2000
      }
    }
  },
  computed: {
    showSwiper () {
      return this.list.length
    }
  }
}
</script>

<style lang="stylus" scoped>
  .wraper >>> .swiper-pagination-bullet
    background rgba(255,255,255,.5)
  .wraper >>> .swiper-pagination-bullet-active
    background #fff
  .wraper
    overflow hidden
    width 100%
    height 0
    padding-bottom 26.26%
    background #e1e1e1
    .swiper_img
      width 100%
      display block
</style>



