<template>
	<div>
		<div class="rec_title">周末去哪儿</div>
		<ul>
			<li class="item" v-for="item in list" :key="item.id">
				<div class="item_img_wrapper">
					<img class="item_img" :src="item.imgUrl" alt="">
				</div>
				<div class="item_info">
					<p class="item_title">{{ item.title }}</p>
					<p class="item_desc">{{ item.desc }}</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'HomeWeekend',
	props: {
		list: Array
	},
	data () {
		return {
			
		}
	}
}	
</script>

<style lang="stylus" scoped>
	@import '~styles/mixins.styl'
	.rec_title
		line-height .8rem
		text-indent .2rem
		background #eee
	.item_img_wrapper
		overflow hidden
		height 0
		padding-bottom 37.86%
		.item_img 
			width 100%
	.item_info
		padding .1rem
		.item_title
			line-height .54rem
			ellipsis()
		.item_desc
			color #616161
			line-height .4rem
			font-size .24rem
			ellipsis()
</style>


