</template>

<script>
export default {
	name: 'CityHeader'
}
</script>

<style lang="stylus" scoped>
	@import '~styles/varibles.styl'
	.header
		position relative
		overflow hidden
		height $headerHeight
		line-height $headerHeight
		color #fff
		font-size .32rem
		text-align center
		background: $bgColor
		.back_icon
			position absolute
			left 0
			top 0
			width .64rem
			font-size .4rem
			color #fff
</style>


