<template>
	<div>
		<div class="search">
			<input type="text" class="search_input" placeholder="请输入关键字" v-model="keywords">
		</div>
		<div class="search_content" ref="content" v-show="keywords">
			<ul>
				<li class="search_item border-bottom" v-for="item in list" :key="item.id" @click="handleCityClick(item)">{{ item.name }}</li>
				<li class="search_item border-bottom" v-show="hasNoData">未找到相关匹配项</li>
			</ul>
		</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'

import { mapMutations } from 'vuex'
export default {
	name: 'CitySearch',
	props: {
		cities: Object
	},
	data () {
		return {
			keywords: '',
			timer: null,
			list: []
		}
	},
	computed: {
		hasNoData () {
			return !this.list.length
		}
	},
	watch: {
		keywords () {
			// 限制查询的执行次数
			if (this.timer) {
				clearTimeout(this.timer)
			}
			if (!this.keywords) {
				this.list = []
				return
			}
			this.timer = setTimeout(() => {
				const result = []
				for (let key in this.cities) {
					this.cities[key].forEach((item) => {
						if (item.spell.indexOf(this.keywords) > -1 || item.name.indexOf(this.keywords) > -1) {
							result.push(item)
						}
					})
				}
				this.list = result
			}, 100)
		}
	},
	methods: {
		...mapMutations(['changeCity']),
	    handleCityClick (city) {
	      // 切换城市
	      this.changeCity(city.name)
	      // 跳转至首页
	      this.$router.push('/')
	    }
	},
	mounted () {
		// 引入bscroll
		this.scroll = new BScroll(this.$refs.content)
	}
}
</script>

<style lang="stylus" scoped>
	@import '~styles/varibles.styl'
	
	.search 
		height .72rem
		padding 0 .1rem
		background $bgColor
		.search_input 
			height .62rem
			width 100%
			padding 0 .1rem
			background #fff
			border-radius .06rem
			text-align center
			color #666
	.search_content
		overflow hidden
		position absolute
		top 1.58rem
		bottom 0
		left 0
		right 0
		z-index 1
		background #eee
		.search_item
			line-height .62rem
			padding-left .2rem
			color #666
			background #fff
</style>


