<template>
	<div>
		<div class="item" v-for="item in list" :key="item.id">
			<div class="title border-bottom">
				<span class="icon"></span>
				{{ item.title }}
			</div>
			<div class="item_child" v-if="item.children">
				<detail-list :list="item.children"></detail-list>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'DetailList',
	props: {
		list: Array
	}
}
</script>

<style lang="stylus" scoped>
	@import '~styles/mixins.styl'
	.item
		.title
			height .8rem
			line-height .8rem
			padding 0 .2rem
			font-size .32rem
			ellipsis()
			.icon
				position relative
				top .06rem
				display inline-block
				width .36rem
				height .36rem
				margin-right .1rem
				background: url('http://s.qunarzz.com/piao/image/touch/sight/detail.png') 0 -.45rem no-repeat
				background-size: .4rem 3rem
		.item_child
			padding-left .2rem
			.title
				font-size .28rem
				color #666
</style>


