<template>
	<ul class="list">
        <li 
            class="item" 
            v-for="item in letters" 
            :key="item"
            :ref="item"
            @click="handleLetterClick"
            @touchstart.prevent="handleTouchStart"
            @touchmove="handleTouchMove"
            @touchend="handleTouchEnd"
        >
            {{ item }}
        </li>
    </ul>
</template>

<script>
export default {
    name: 'CityAlphabet',
    props: {
        cities: Object
    },
    data () {
        return {
            startStatus: false,
            startY: 0,
            timer: null
        }
    },
    updated () {
        this.startY = this.$refs['A'][0].offsetTop
    },
    computed: {
        letters () {
            const letters = []
            for (let key in this.cities) {
                letters.push(key)
            }
            return letters
        }
    },
    methods: {
        handleLetterClick (ev) {
            this.$emit('change', ev.target.innerText)
        },
        handleTouchStart () {
            this.startStatus = true
        },
        handleTouchMove (ev) {
            // 限制handleTouchMove函数的执行频率
            if (this.timer) {
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(() => {
                if (this.startStatus) {
                    let touchY = ev.touches[0].clientY - 79
                    let index = Math.floor((touchY - this.startY) / 20)
                    if (index >= 0 && index < this.letters.length) {
                        this.$emit('change', this.letters[index])
                    }
                }
            }, 16)
        },
        handleTouchEnd () {
            this.startStatus = false
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~styles/varibles.styl'

    .list
        position absolute
        top 1.58rem
        bottom 0
        right 0
        width .4rem
        display flex
        flex-direction column
        justify-content center
        .item
            text-align center
            line-height .4rem
            color $bgColor

</style>


