<template>
    <div class="header">
        <div class="header_left">
          <i class="iconfont back_icon">&#xe624;</i>
        </div>
        <div class="header_input">
          <i class="iconfont">&#xe632;</i>
          <input type="text" placeholder="输入城市/景点/游玩主题">
        </div>
        <router-link to="/city">
          <div class="header_right">
            {{ city }}
            <i class="iconfont">&#xe6aa;</i>
          </div>
        </router-link>
    </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city']),
    ...mapGetters(['filterCity'])
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl';

  .header
    height $headerHeight
    line-height $headerHeight
    color #fff
    background $bgColor
    display flex
    align-items center
    .header_left
      width .64rem
      .back_icon
        display block
        text-align center
        font-size .4rem
    .header_input
      flex 1
      height .64rem
      line-height .64rem
      margin-left .1rem
      color #999
      position relative
      .iconfont
        position absolute
        left 0.04rem
        top 0
      input
        width 100%
        height 100%
        border-radius .1rem
        padding-left .4rem
    .header_right
      min-width 1.24rem
      padding 0 .1rem
      text-align center
      color #fff
      .iconfont
        margin-left -0.04rem
</style>



