<template>
	<div>
		<router-link to="/" class="header_abs" v-show="showAbs">
			<i class="iconfont header_abs_icon">&#xe624;</i>
		</router-link>
		<div class="header_fixed" v-show="!showAbs" :style="opacityStyle">
			{{ detailName }}
			<router-link to="/">
				<i class="iconfont header_fixed_icon">&#xe624;</i>
			</router-link>
		</div>
	</div>
</template>

<script>
export default {
	name: 'DetailHeader',
	props: {
		detailName: String
	},
	data () {
		return {
			showAbs: true,
			opacityStyle: {
				opacity: 0
			}
		}
	},
	methods: {
		handleScroll () {
			console.log(this.showAbs)
			const sTop = document.documentElement.scrollTop || document.body.scrollTop
			if (sTop > 10) {
				this.showAbs = false
				let opacity = sTop / 206
				opacity = opacity > 1 ? 1 : opacity
				this.opacityStyle = { opacity }
			} else {
				this.showAbs = true
			}
		}
	},
	mounted () {
		// 监听屏幕滚动事件
		window.addEventListener('scroll', this.handleScroll)
	},
	beforeDestroy () {
		// 解除屏幕滚动事件
		window.removeEventListener('scroll', this.handleScroll)
	}
}
</script>

<style lang="stylus" scoped>
	@import '~styles/varibles.styl'
	
	.header_abs
		position absolute
		z-index 1
		top .2rem
		left .2rem
		width .72rem
		height .72rem
		line-height .72rem
		text-align center
		border-radius 50%
		background rgba(0, 0, 0, .5)
		.header_abs_icon
			color #fff
			font-size .36rem
			font-weight bold
	.header_fixed
		position fixed
		z-index 1
		top 0
		left 0
		right 0
		height $headerHeight
		line-height $headerHeight
		color #fff
		font-size .32rem
		text-align center
		background: $bgColor
		.header_fixed_icon
			position absolute
			left 0
			top 0
			width .64rem
			font-size .4rem
			color #fff
</style>


