<template>
	<div class="list" ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom">您的位置</div>
        <div class="button_list">
          <div class="button_wrap">
            <div class="button">{{ city }}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button_list">
          <div class="button_wrap" v-for="item in hot" :key="item.id" @click="handleCityClick(item)">
            <div class="button">{{ item.name }}</div>
          </div>
        </div>
      </div>
      <div class="area" v-for="(item, key) in cities" :key="key" :ref="key">
        <div class="title border-topbottom">{{ key }}</div>
        <ul class="item_list">
          <li class="item border-bottom" v-for="innerItem in item" :key="innerItem.id" @click="handleCityClick(innerItem)">{{ innerItem.name }}</li>
        </ul>
      </div>
    </div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'

import { mapState, mapMutations } from 'vuex'
export default {
  name: 'CityList',
  props: {
    hot: Array,
    cities: Object,
    letter: String
  },
  computed: {
    ...mapState(['city'])
  },
  watch: {
    letter () {
      const element = this.$refs[this.letter][0]
      this.scroll.scrollToElement(element)
    }
  },
  methods: {
    ...mapMutations(['changeCity']),
    handleCityClick (city) {
      // 切换城市
      this.changeCity(city.name)
      // 跳转至首页
      this.$router.push('/')
    }
  },
  mounted () {
    // 创建better-scroll实例
    this.scroll = new BScroll(this.$refs.wrapper)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'

  .border-topbottom
    &:before, &:after 
      border-color $borderColor
  .border-bottom
    &:before
      border-color $borderColor

  .list
    position absolute
    left 0
    right 0
    top 1.58rem
    bottom 0
    overflow hidden
    .title
      height .44rem
      line-height .44rem
      padding-left .2rem
      color #666
      font-size .26rem
      background #eee
    .button_list
      overflow hidden
      padding .1rem .6rem .1rem .1rem
      .button_wrap
        float left
        width 33.3%
        .button
          margin .1rem
          line-height .5rem
          text-align center
          border solid .02rem #ccc
          border-radius .06rem
    .item_list
      .item
        line-height .76rem
        padding-left .2rem    
        
</style>




