<template>
	<div>
		<div class="rec_title">热销推荐</div>
		<ul>
			<router-link tag="li" :to="'/detail/' + item.id" class="item border-bottom" v-for="item in list" :key="item.id">
				<img class="item_img" :src="item.imgUrl" alt="">
				<div class="item_info">
					<p class="item_title">{{ item.title }}</p>
					<p class="item_desc">{{ item.desc }}</p>
					<button type="button" class="item_btn">查看详情</button>
				</div>
			</router-link>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'HomeRecommend',
	props: {
		list: Array
	},
	data () {
		return {
			
		}
	}
}	
</script>

<style lang="stylus" scoped>
	@import '~styles/mixins.styl'
	.rec_title
		margin-top .2rem
		line-height .8rem
		text-indent .2rem
		background #eee
	.item 
		overflow hidden	
		display flex
		height 1.9rem
		.item_img 
			width 1.9rem
			height 1.9rem
			padding .1rem
		.item_info 
			flex 1
			padding .1rem
			min-width 0
			.item_title 
				font-size .32rem
				line-height .54rem
				ellipsis()
			.item_desc
				color #f55
				line-height .4rem
				ellipsis()
			.item_btn
				background #ff9300
				color #fff
				padding 0 .2rem
				border-radius .06rem
				line-height .44rem
				margin-top .16rem
</style>


