<template>
	<div class="container" @click="handleGallaryClick">
		<div class="wrapper">
			<swiper :options="swiperOption">
				<swiper-slide v-for="(item, index) in imgs" :key="index">
					<img class="gallary_img" :src="item" alt="error!">
				</swiper-slide>
				<div class="swiper-pagination" slot="pagination"></div>
		    </swiper>
		</div>
	</div>
</template>

<script>
export default {
	name: 'CommonGallary',
	props: {
		imgs: {
			type: Array,
			default: []
		}
	},
	data () {
		return {
			swiperOption: {
				pagination: '.swiper-pagination',
				paginationType: 'fraction',
				observeParents: true,
				observer: true
			}
		}
	},
	methods: {
		handleGallaryClick () {
			this.$emit('closeGallary')
		}
	}
}
</script>

<style lang="stylus" scoped>
	.swiper-container
		overflow inherit
		.swiper-pagination
			bottom -1rem
			color #fff
			
	.container
		position fixed
		left 0
		right 0
		top 0
		bottom 0
		z-index 99
		background #000
		display flex
		flex-direction column
		justify-content center
		.wrapper
			width 100%
			height 0
			padding-bottom 65%
			.gallary_img
				width 100%
				
</style>


