<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page, index) in pages" :key="index">
        <div class="icon" v-for="item in page" :key="item.id">
          <div class="img_wrap">
            <img :src="item.imgUrl" alt="">
          </div>
          <p class="icon_des">{{ item.desc }}</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false,
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      let pages = []
      this.list.forEach((item, index) => {
        let page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })

      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'

  .icons
    margin-top: .1rem
    .swiper-container
      height 0
      padding-bottom 50%
    .icon
      position relative
      float left
      width 25%
      height 0
      padding-bottom 25%
      .img_wrap
        overflow hidden
        position absolute
        left 0
        right 0
        top 0
        bottom .4rem
        padding .1rem
        img
          height 100%
          display block
          margin 0 auto
      .icon_des
        position absolute
        left 0
        right 0
        bottom 0
        height .4rem
        line-height .4rem
        text-align center
        color $darkTextColor
        ellipsis()
</style>



