<template>
	<div>
		<div class="banner" @click="handleShowGallary">
			<img class="banner-img" :src="bannerImg" alt="">
			<div class="banner-info">
				<div class="banner-title">{{ sightName }}</div>
				<div class="banner-num">
					<i class="iconfont banner_icon">&#xe63e;</i>
					{{ gallaryImgs.length }}
				</div>
			</div>
		</div>
		<fade-animation>
			<common-gallary :imgs="gallaryImgs" v-show="showGallary" @closeGallary="handleHideGallary"></common-gallary>
		</fade-animation>
	</div>
</template>

<script>
import FadeAnimation from 'common/animation/FadeAnimation'
import CommonGallary from 'common/gallary/Gallary'
export default {
	name: 'DetailBanner',
	components: {
		CommonGallary,
		FadeAnimation
	},
	props: {
		sightName: String,
		bannerImg: String,
		gallaryImgs: Array
	},
	data () {
		return {
			showGallary: false
		}
	},
	methods: {
		handleShowGallary () {
			this.showGallary = true
		},
		handleHideGallary () {
			this.showGallary = false
		}
	}
}
</script>

<style lang="stylus" scoped>
	@import '~styles/mixins.styl'

	.banner
		position relative
		overflow hidden
		height 0
		padding-bottom 55%
		.banner-img
			width 100%
		.banner-info
			position absolute
			left 0
			right 0
			bottom 0
			height .6rem
			color #fff
			display flex
			align-items center
			background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, .8))
			.banner-title
				flex 1
				padding 0 .2rem
				font-size .32rem
				ellipsis()
			.banner-num
				padding 0 .2rem
				width 1.2rem
				height .4rem
				line-height .4rem
				border-radius .2rem
				background rgba(0, 0, 0, .5)
				text-align center
				font-size .24rem
				margin-right .2rem
				.banner_icon
					font-size .28rem
</style>


